* {
    box-sizing: border-box;
}

body {
    margin: 0;
    background-color: #0B2434;
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    font-family: 'Karla', sans-serif;
}

.title {
    font-size: 40px;
    margin: 0;
}

.instructions {
    font-family: 'Inter', sans-serif;
    font-weight: 400;
    margin-top: 0;
    text-align: center;
}
.win-instructions{
    font-family: 'Inter', sans-serif;
    font-weight: 400;
    margin-top: 0;
    text-align: center;
    color: rgb(187, 10, 10);
    font-size: 2rem;
    font-weight:bold;
    font-family:fantasy
}

main {
    
    background-color: #e7e3e3;
    height: 500px;
    width: 550px;
    text-align: center;
    border-radius: 5px;
    padding: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}

@media only screen and (max-width: 600px) {
    .title {
        font-size: 40px;
        margin-bottom: 20px;
    }

    main {
        margin: 0;
        padding: 10px;
        background-color: #e7e3e3;
        height: 500px;
        width: 280px;
        text-align: center;
        border-radius: 5px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    
}


.dice-container{
    
    display: grid;
    grid-template: auto auto / repeat(4, 1fr);
    gap: 20px;
    padding-bottom: 20px;
    
}
.die-face{
    height: 50px;
    width: 50px;
    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    
}

.die-num{
    font-size: 2rem;
}

.roll-dice{
    height: 50px;
    width: 200px;
    border-radius: 6px;
    background-color: #5035FF;
    cursor: pointer;
    color: rgb(248, 248, 250);
    font-size: 2rem;
    font-family: 'Karla', sans-serif;
}

.roll-dice:focus {
    outline: none;
}

.roll-dice:active {
    box-shadow: inset 5px 5px 10px -3px rgba(0, 0, 0, 0.7);
}

.win-text{
    height: 50px;
    max-width: 500px;
    border-radius: 6px;
    background-color: #d43a5b;
    cursor: pointer;
    color: rgb(248, 248, 250);
    font-size: 1 rem;
    font-family: 'Karla', sans-serif;

}
@media only screen and (max-width: 600px) {
    .roll-dice{
        height: 50px;
        width: 150px;
        border-radius: 6px;
        background-color: #5035FF;
        cursor: pointer;
        color: rgb(248, 248, 250);
        font-size: 1.5rem;
        font-family: 'Karla', sans-serif;
    }
    .dice-container{
    
        display: grid;
        grid-template: auto auto / repeat(4, 1fr);
        gap: 15px;
        padding-bottom: 20px;
    }

    .win-instructions{
        font-family: 'Inter', sans-serif;
        font-weight: 300;
        margin-top: 0;
        text-align: center;
        color: rgb(187, 10, 10);
        font-size: 1.5rem;
        font-weight:bold;
        font-family:fantasy
    }

}
